.node {
  height: 30px;
  width: 30px;
  border: 1px solid #90cbff;
  background-color: #fff;
}

.start {
  background-image: url(/pathfinder/static/media/startnode.9cd7c17b.svg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  animation-name: specialNodes;
  animation-duration: 1s;
  animation-timing-function: ease-out;
  animation-delay: 0;
  animation-direction: alternate;
  animation-iteration-count: 1;
  animation-fill-mode: forwards;
  animation-play-state: running;
}

.target {
  background-image: url(/pathfinder/static/media/finishnode.465c59c9.svg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  animation-name: specialNodes;
  animation-duration: 1s;
  animation-timing-function: ease-out;
  animation-delay: 0;
  animation-direction: alternate;
  animation-iteration-count: 1;
  animation-fill-mode: forwards;
  animation-play-state: running;
}

.wall {
  /* background-color: #2b3661; */
  border: none;
  animation-name: wallAnimation;
  animation-duration: 0.4s;
  animation-timing-function: ease-out;
  animation-delay: 0;
  animation-direction: alternate;
  animation-iteration-count: 1;
  animation-fill-mode: forwards;
  animation-play-state: running;
}

.weight {
  background-image: url(/pathfinder/static/media/weightnode.781c369f.svg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  animation-name: specialNodes;
  animation-duration: 1s;
  animation-timing-function: ease-out;
  animation-delay: 0;
  animation-direction: alternate;
  animation-iteration-count: 1;
  animation-fill-mode: forwards;
  animation-play-state: running;
}

.visited {
  animation-name: visitedAnimation;
  animation-duration: 1.5s;
  animation-timing-function: ease-out;
  animation-delay: 0;
  animation-direction: alternate;
  animation-iteration-count: 1;
  animation-fill-mode: forwards;
  animation-play-state: running;
}

.node-shortest-path {
  animation-name: shortestPath;
  animation-duration: 1.5s;
  animation-timing-function: ease;
  animation-delay: 0;
  animation-direction: alternate;
  animation-iteration-count: 1;
  animation-fill-mode: forwards;
  animation-play-state: running;
}

@keyframes visitedAnimation {
  0% {
    transform: scale(0.3);
    background-color: #283048;
    border-radius: 100%;
  }

  25% {
    background-color: #333d5b;
  }

  50% {
    background-color: #3b466a;
  }

  75% {
    transform: scale(1.2);
    background-color: #6e7ba7;
  }

  100% {
    transform: scale(1);
    background-color: #758bcd;
  }
}

@keyframes specialNodes {
  0% {
    transform: scale(0.3);
  }

  50% {
    transform: scale(0.8);
    /*background-color: darkslategrey;*/
  }

  100% {
    transform: scale(1);
    /*background-color: darkslategrey;*/
  }
}

@keyframes wallAnimation {
  0% {
    transform: scale(0.3);
    background-color: #5569b6;
  }

  50% {
    transform: scale(1.2);
    background-color: #3a477b;
  }

  100% {
    transform: scale(1);
    background-color: #181f35;
  }
}

@keyframes shortestPath {
  0% {
    transform: scale(0.7);
    background-color: #60da8d;
  }

  50% {
    transform: scale(1.2);

    background-color: #60da8d;
  }

  100% {
    transform: scale(1);

    background-color: #60da8d;
  }
}

